import {
  Container,
  Box,
  Typography,
  TextField,
  Button,
  List,
  ListItem,
  Paper,
  IconButton,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import SaveIcon from "@mui/icons-material/Save";
import DragIndicatorIcon from "@mui/icons-material/DragIndicator";
import { useState, useEffect } from "react";
import {
  DragDropContext,
  Droppable,
  Draggable,
  DropResult,
} from "react-beautiful-dnd";

interface ShoppingList {
  name: string;
  items: string[];
}
export default function Shopping() {
  const [lists, setLists] = useState<ShoppingList[]>([]);
  const [newListName, setNewListName] = useState("");
  const [itemInputs, setItemInputs] = useState<string[]>([]); // one input per list
  const [editingListIdx, setEditingListIdx] = useState<number | null>(null);
  const [editingListName, setEditingListName] = useState("");
  const [editingItem, setEditingItem] = useState<{
    listIdx: number;
    itemIdx: number;
    value: string;
  } | null>(null);

  // Load from localStorage
  useEffect(() => {
    const saved = localStorage.getItem("shoppingLists");
    if (saved) {
      const parsed = JSON.parse(saved);
      setLists(parsed);
      setItemInputs(parsed.map(() => ""));
    }
  }, []);
  // Save to localStorage
  useEffect(() => {
    localStorage.setItem("shoppingLists", JSON.stringify(lists));
  }, [lists]);

  const handleAddList = () => {
    if (newListName.trim() === "") return;
    setLists([...lists, { name: newListName.trim(), items: [] }]);
    setItemInputs([...itemInputs, ""]);
    setNewListName("");
  };

  const handleItemInputChange = (idx: number, value: string) => {
    setItemInputs((inputs) => inputs.map((v, i) => (i === idx ? value : v)));
  };

  const handleAddItem = (listIdx: number) => {
    const item = itemInputs[listIdx]?.trim();
    if (!item) return;
    setLists((lists) =>
      lists.map((list, idx) =>
        idx === listIdx ? { ...list, items: [...list.items, item] } : list
      )
    );
    setItemInputs((inputs) => inputs.map((v, i) => (i === listIdx ? "" : v)));
  };

  const handleRemoveItem = (listIdx: number, itemIdx: number) => {
    setLists((lists) =>
      lists.map((list, idx) =>
        idx === listIdx
          ? { ...list, items: list.items.filter((_, i) => i !== itemIdx) }
          : list
      )
    );
  };

  // Edit list name
  const handleEditListName = (idx: number) => {
    setEditingListIdx(idx);
    setEditingListName(lists[idx].name);
  };
  const handleSaveListName = (idx: number) => {
    setLists((lists) =>
      lists.map((l, i) =>
        i === idx ? { ...l, name: editingListName.trim() } : l
      )
    );
    setEditingListIdx(null);
    setEditingListName("");
  };

  // Edit item
  const handleEditItem = (listIdx: number, itemIdx: number) => {
    setEditingItem({ listIdx, itemIdx, value: lists[listIdx].items[itemIdx] });
  };
  const handleSaveItem = () => {
    if (!editingItem) return;
    setLists((lists) =>
      lists.map((l, li) =>
        li === editingItem.listIdx
          ? {
              ...l,
              items: l.items.map((it, ii) =>
                ii === editingItem.itemIdx ? editingItem.value : it
              ),
            }
          : l
      )
    );
    setEditingItem(null);
  };

  // Drag and drop
  const onDragEnd = (result: DropResult) => {
    if (!result.destination) return;
    // Reorder lists
    if (result.type === "LIST") {
      const newLists = Array.from(lists);
      const [removed] = newLists.splice(result.source.index, 1);
      newLists.splice(result.destination.index, 0, removed);
      setLists(newLists);
      // Also reorder itemInputs
      const newInputs = Array.from(itemInputs);
      const [inputRemoved] = newInputs.splice(result.source.index, 1);
      newInputs.splice(result.destination.index, 0, inputRemoved);
      setItemInputs(newInputs);
    }
    // Reorder items within a list
    if (result.type === "ITEM") {
      const listIdx = parseInt(result.source.droppableId.replace("list-", ""));
      const items = Array.from(lists[listIdx].items);
      const [removed] = items.splice(result.source.index, 1);
      items.splice(result.destination.index, 0, removed);
      setLists((lists) =>
        lists.map((l, i) => (i === listIdx ? { ...l, items } : l))
      );
    }
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
        Zakupy
      </Typography>
      <Box display="flex" gap={2} mb={3}>
        <TextField
          label="Nazwa nowej listy"
          value={newListName}
          onChange={(e) => setNewListName(e.target.value)}
          size="small"
        />
        <Button
          variant="contained"
          onClick={handleAddList}
          disabled={!newListName.trim()}
        >
          Dodaj list
        </Button>
      </Box>
      <Box>
        <DragDropContext onDragEnd={onDragEnd}>
          <Droppable droppableId="lists-droppable" type="LIST">
            {(provided) => (
              <div ref={provided.innerRef} {...provided.droppableProps}>
                {lists.length === 0 ? (
                  <Typography color="text.secondary">
                    Brak list zakupw.
                  </Typography>
                ) : (
                  <List>
                    {lists.map((list, idx) => (
                      <Draggable
                        draggableId={`list-${idx}`}
                        index={idx}
                        key={idx}
                      >
                        {(provided, snapshot) => (
                          <ListItem
                            ref={provided.innerRef}
                            {...provided.draggableProps}
                            disablePadding
                            sx={{ mb: 2 }}
                          >
                            <Paper sx={{ width: "100%", p: 2, mb: 0 }}>
                              <Box display="flex" alignItems="center" mb={1}>
                                <span {...provided.dragHandleProps}>
                                  <DragIndicatorIcon
                                    sx={{
                                      mr: 1,
                                      color: snapshot.isDragging
                                        ? "primary.main"
                                        : "action.disabled",
                                    }}
                                  />
                                </span>
                                {editingListIdx === idx ? (
                                  <TextField
                                    value={editingListName}
                                    onChange={(e) =>
                                      setEditingListName(e.target.value)
                                    }
                                    size="small"
                                    sx={{ flex: 1, mr: 1 }}
                                    onKeyDown={(e) => {
                                      if (e.key === "Enter")
                                        handleSaveListName(idx);
                                    }}
                                    autoFocus
                                  />
                                ) : (
                                  <Typography variant="h6" sx={{ flex: 1 }}>
                                    {list.name}
                                  </Typography>
                                )}
                                {editingListIdx === idx ? (
                                  <IconButton
                                    onClick={() => handleSaveListName(idx)}
                                    size="small"
                                  >
                                    <SaveIcon />
                                  </IconButton>
                                ) : (
                                  <IconButton
                                    onClick={() => handleEditListName(idx)}
                                    size="small"
                                  >
                                    <EditIcon />
                                  </IconButton>
                                )}
                              </Box>
                              <Box display="flex" gap={1} mt={1} mb={1}>
                                <TextField
                                  label="Dodaj produkt"
                                  value={itemInputs[idx] || ""}
                                  onChange={(e) =>
                                    handleItemInputChange(idx, e.target.value)
                                  }
                                  size="small"
                                  sx={{ flex: 1 }}
                                />
                                <Button
                                  variant="outlined"
                                  onClick={() => handleAddItem(idx)}
                                  disabled={
                                    !(itemInputs[idx] && itemInputs[idx].trim())
                                  }
                                >
                                  Dodaj
                                </Button>
                              </Box>
                              <Droppable
                                droppableId={`list-${idx}`}
                                type="ITEM"
                              >
                                {(provided) => (
                                  <div
                                    ref={provided.innerRef}
                                    {...provided.droppableProps}
                                  >
                                    {list.items.length === 0 ? (
                                      <Typography
                                        color="text.secondary"
                                        fontSize={14}
                                      >
                                        (Brak produktw na licie)
                                      </Typography>
                                    ) : (
                                      <List sx={{ pl: 2 }}>
                                        {list.items.map((item, itemIdx) => (
                                          <Draggable
                                            draggableId={`item-${idx}-${itemIdx}`}
                                            index={itemIdx}
                                            key={itemIdx}
                                          >
                                            {(provided, snapshot) => (
                                              <ListItem
                                                ref={provided.innerRef}
                                                {...provided.draggableProps}
                                                sx={{
                                                  p: 0,
                                                  mb: 0.5,
                                                  display: "flex",
                                                  alignItems: "center",
                                                  background:
                                                    snapshot.isDragging
                                                      ? "#f0f0f0"
                                                      : undefined,
                                                }}
                                              >
                                                <span
                                                  {...provided.dragHandleProps}
                                                >
                                                  <DragIndicatorIcon
                                                    sx={{
                                                      mr: 1,
                                                      color: snapshot.isDragging
                                                        ? "primary.main"
                                                        : "action.disabled",
                                                    }}
                                                  />
                                                </span>
                                                {editingItem &&
                                                editingItem.listIdx === idx &&
                                                editingItem.itemIdx ===
                                                  itemIdx ? (
                                                  <TextField
                                                    value={editingItem.value}
                                                    onChange={(e) =>
                                                      setEditingItem({
                                                        ...editingItem,
                                                        value: e.target.value,
                                                      })
                                                    }
                                                    size="small"
                                                    sx={{ flex: 1, mr: 1 }}
                                                    onKeyDown={(e) => {
                                                      if (e.key === "Enter")
                                                        handleSaveItem();
                                                    }}
                                                    autoFocus
                                                  />
                                                ) : (
                                                  <Typography sx={{ flex: 1 }}>
                                                    {item}
                                                  </Typography>
                                                )}
                                                {editingItem &&
                                                editingItem.listIdx === idx &&
                                                editingItem.itemIdx ===
                                                  itemIdx ? (
                                                  <IconButton
                                                    onClick={handleSaveItem}
                                                    size="small"
                                                  >
                                                    <SaveIcon />
                                                  </IconButton>
                                                ) : (
                                                  <IconButton
                                                    onClick={() =>
                                                      handleEditItem(
                                                        idx,
                                                        itemIdx
                                                      )
                                                    }
                                                    size="small"
                                                  >
                                                    <EditIcon />
                                                  </IconButton>
                                                )}
                                                <Button
                                                  color="error"
                                                  size="small"
                                                  onClick={() =>
                                                    handleRemoveItem(
                                                      idx,
                                                      itemIdx
                                                    )
                                                  }
                                                >
                                                  Usu
                                                </Button>
                                              </ListItem>
                                            )}
                                          </Draggable>
                                        ))}
                                        {provided.placeholder}
                                      </List>
                                    )}
                                  </div>
                                )}
                              </Droppable>
                            </Paper>
                          </ListItem>
                        )}
                      </Draggable>
                    ))}
                    {provided.placeholder}
                  </List>
                )}
              </div>
            )}
          </Droppable>
        </DragDropContext>
      </Box>
    </Container>
  );
}
