import { RestaurantMenu } from "@mui/icons-material";
import MenuIcon from "@mui/icons-material/Menu";
import AppBar from "@mui/material/AppBar";
import Avatar from "@mui/material/Avatar";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Container from "@mui/material/Container";
import IconButton from "@mui/material/IconButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Toolbar from "@mui/material/Toolbar";
import Tooltip from "@mui/material/Tooltip";
import Typography from "@mui/material/Typography";
import { Session } from "@supabase/supabase-js";
import { useState } from "react";
import { NAV_TABS } from "./navigation";
import { supabase } from "./supabaseClient";

const PAGES = NAV_TABS;

interface AppNavigationBarProps {
  session: Session;
  tab: number;
  onTabChange: (index: number) => void;
}

export default function AppNavigationBar({
  session,
  tab,
  onTabChange,
}: AppNavigationBarProps) {
  const [anchorElNav, setAnchorElNav] = useState<null | HTMLElement>(null);
  const [anchorElUser, setAnchorElUser] = useState<null | HTMLElement>(null);

  // Logo rendering for both desktop and mobile
  const Logo = ({ variant }: { variant: "desktop" | "mobile" }) => (
    <Box
      sx={{
        display: {
          xs: variant === "mobile" ? "flex" : "none",
          md: variant === "desktop" ? "flex" : "none",
        },
        alignItems: "center",
        cursor: "pointer",
        mr: 2,
      }}
      onClick={() => onTabChange(0)}
    >
      <RestaurantMenu sx={{ mr: 1 }} />
      <Typography
        variant={variant === "desktop" ? "h6" : "h5"}
        noWrap
        sx={{
          fontFamily: "monospace",
          fontWeight: 700,
          letterSpacing: ".3rem",
          color: "inherit",
          textDecoration: "none",
        }}
      >
        FOODTIME
      </Typography>
    </Box>
  );

  // Navigation menu for mobile
  const renderMobileMenu = () => (
    <Box sx={{ flexGrow: 1, display: { xs: "flex", md: "none" } }}>
      <IconButton
        size="large"
        aria-label="account of current user"
        aria-controls="menu-appbar"
        aria-haspopup="true"
        onClick={(e) => setAnchorElNav(e.currentTarget)}
        color="inherit"
      >
        <MenuIcon />
      </IconButton>
      <Menu
        id="menu-appbar"
        anchorEl={anchorElNav}
        anchorOrigin={{ vertical: "bottom", horizontal: "left" }}
        keepMounted
        transformOrigin={{ vertical: "top", horizontal: "left" }}
        open={Boolean(anchorElNav)}
        onClose={() => setAnchorElNav(null)}
        sx={{ display: { xs: "block", md: "none" } }}
      >
        {PAGES.map((tabObj) => (
          <MenuItem
            key={tabObj.label}
            selected={tab === tabObj.index}
            onClick={() => {
              onTabChange(tabObj.index);
              setAnchorElNav(null);
            }}
          >
            <Typography sx={{ textAlign: "center" }}>{tabObj.label}</Typography>
          </MenuItem>
        ))}
      </Menu>
    </Box>
  );

  // Navigation menu for desktop
  const renderDesktopMenu = () => (
    <Box sx={{ flexGrow: 1, display: { xs: "none", md: "flex" } }}>
      {PAGES.map((tabObj) => (
        <Button
          key={tabObj.label}
          onClick={() => onTabChange(tabObj.index)}
          sx={{
            my: 2,
            color: tab === tabObj.index ? "#FFD700" : "white",
            display: "block",
            fontWeight: tab === tabObj.index ? 700 : 400,
            borderBottom: tab === tabObj.index ? "2px solid #FFD700" : "none",
          }}
        >
          {tabObj.label}
        </Button>
      ))}
    </Box>
  );

  // User menu
  const handleLogout = () => {
    setAnchorElUser(null);
    supabase.auth.signOut();
  };
  const settings = [
    {
      name: "Profil",
      func: () => {
        setAnchorElUser(null);
        onTabChange(-1);
      },
    },
    { name: "Wyloguj", func: handleLogout },
  ];

  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Logo variant="desktop" />
          {renderMobileMenu()}
          <Logo variant="mobile" />
          {renderDesktopMenu()}
          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="Open settings">
              <IconButton
                onClick={(e) => setAnchorElUser(e.currentTarget)}
                sx={{ p: 0 }}
              >
                <Avatar
                  alt={session.user.email}
                  src={session.user.user_metadata?.picture}
                />
              </IconButton>
            </Tooltip>
            <Menu
              sx={{ mt: "45px" }}
              id="menu-appbar"
              anchorEl={anchorElUser}
              anchorOrigin={{ vertical: "top", horizontal: "right" }}
              keepMounted
              transformOrigin={{ vertical: "top", horizontal: "right" }}
              open={Boolean(anchorElUser)}
              onClose={() => setAnchorElUser(null)}
            >
              {settings.map((element) => (
                <MenuItem key={element.name} onClick={element.func}>
                  <Typography sx={{ textAlign: "center" }}>
                    {element.name}
                  </Typography>
                </MenuItem>
              ))}
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
