import React from "react";
import Account from "./Account";
import Calculator from "./Calculator";
import Recipes from "./Recipes";
import Shopping from "./Shopping";
import Calendar from "./Calendar";

export interface NavTab {
  label: string;
  index: number;
}

export const NAV_TABS: NavTab[] = [
  { label: "Kalendarz", index: 0 },
  { label: "Zakupy", index: 1 },
  { label: "Przepisy", index: 2 },
  { label: "Kalkulator", index: 3 },
];

export function getTabContent(tab: number, session: any): React.ReactNode {
  if (tab === -1) {
    return <Account session={session} />;
  } else if (tab === 0) {
    return <Calendar />;
  } else if (tab === 1) {
    return <Shopping />;
  } else if (tab === 2) {
    return <Recipes />;
  } else if (tab === 3) {
    return <Calculator />;
  }
  return null;
}
